* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

button {
  color: #fff;
  cursor: pointer;
  z-index: 0;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background: #111;
  border: none;
  border-radius: 10px;
  outline: none;
  padding: .6em 2em;
  position: relative;
}

button:before {
  content: "";
  z-index: -1;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  background: linear-gradient(45deg, red, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, red) 0 0 / 400%;
  border-radius: 10px;
  transition: opacity .3s ease-in-out;
  animation: glowing-button-85 20s linear infinite;
  position: absolute;
  top: -2px;
  left: -2px;
}

@keyframes glowing-button-85 {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 400% 0;
  }

  100% {
    background-position: 0 0;
  }
}

button:after {
  z-index: -1;
  content: "";
  width: 100%;
  height: 100%;
  background: #222;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
}

a {
  width: 150px;
  color: #000;
  cursor: pointer;
  text-align: center;
  box-sizing: border-box;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background-color: #fff000;
  border: 0;
  border-radius: 12px;
  margin-bottom: 10px;
  padding: 10px 15px;
  font-size: 16px;
  font-weight: bold;
  transition: all .2s;
}

a:not(:disabled):hover, a:not(:disabled):focus {
  background: green;
  outline: 0;
  box-shadow: 0 0 0 2px rgba(0, 0, 0, .2), 0 3px 8px rgba(0, 0, 0, .15);
}

a:disabled {
  filter: saturate(.2) opacity(.5);
  -webkit-filter: saturate(.2) opacity(.5);
  cursor: not-allowed;
}

li {
  display: flex;
}

